.verte {
  width: 84px;
  height: 60px;
  /deep/.verte__guide {
    width: 100% !important;
    height: 100% !important;
  }
}
.image {
  $main: &;
  position: relative;
  overflow: hidden;
  &__input-label {
    font-weight: 300;
    cursor: pointer;
    position: absolute;
    z-index: 10;
    color: white;
    margin: 1rem;
    opacity: 0.3;
    transition: all 0.3s ease-in-out;
    #{$main}:hover & {
      opacity: 1;
    }
  }
  &__file-input {
    outline: none;
    margin-left: -400px;
    &::-webkit-file-upload-button {
      visibility: hidden;
    }
    &::before {
      content: "";
      display: inline-block;
      background: transparent;
      width: 100%;
      height: calc(100% - 29px);
      position: absolute;
      top: 0;
      left: 0;
      outline: none;
      white-space: nowrap;
      -webkit-user-select: none;
      cursor: pointer;
    }
    &:hover::before {
    }
    &:active::before {
    }
  }
  &__preview {
    display: block;
    width: 100%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
  }
}
.clip-filter {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 0;
  height: 0;
  left: -9999px;
}
